<template>
    <div class="card" :class="isRequest ? 'request' : ''">

        <div class="info">
            <div class="title">{{ account.nickname }}</div>
            <div class="right info-icon" @click.stop="clientDetailsClick(account.clientId)"></div>
        </div>

        <div class="actions">
            <button class="blue-button" @click="getControl()" v-if="isRequest">{{ $localizeFilter('Взять управление') }}</button>
            <button class="blue-button" @click="goToAccount()" v-else>{{ $localizeFilter('Перейти в аккаунт') }}</button>
            <!-- <button :class="!isRequest ? 'blue-button red': ''">{{ $localizeFilter('Отказаться от управления') }}</button> -->

            <button class="blue-button red" :class="isRequest ? '' : 'link'" @click="isRequest ? refuseControl() : cancelControl()">{{ $localizeFilter('Отказаться от управления') }}</button>
        </div>

    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'


export default defineComponent({
    setup() {

    },
    data: () => ({

    }),
    props: ['account', 'isRequest'],

    methods: {
        clientDetailsClick(clientId) {
            this.$emit('openClientDetails', clientId)
        },
        getControl(){
            this.$emit('getControl')
        },
        refuseControl(){
            this.$emit('refuseControl')
        },
        goToAccount(){
            this.$emit('goToAccount')
        },
        cancelControl(){
            this.$emit('cancelControl')
        },




    }
})
</script>

