<template>
<div class="structure">
    <p class="title">{{$localizeFilter('accounts_in_control')}}</p>

    <div class="search">
      <input type="text" v-model.trim="searchString" class="custom-input" :placeholder="$localizeFilter('placeholder_login_or_partner_name')">
      <button class="blue-button" @click="startSearch">{{ $localizeFilter('search_button') }}</button>
    </div>
    
     <!-- <Tree v-if="structure" key="tree"
     class="tree"
     v-bind:nodes="structure"
     :indentSize="50" @openClientDetails="openClientDetails" /> -->

    <p class="items-header" v-if="requests.length">Заявки: {{ requests.length }}</p>

    <AccountControlCard v-for="(account, index) in requests" v-bind:key="index" :account="account" :isRequest="true"
    @openClientDetails="openClientDetails"
    @getControl="getControl(account.requestId)"
    @refuseControl="refuseControl(account.requestId)" />

    <p class="items-header" v-if="managedAccounts.length">Аккаунты в управлении: {{ visibleManagedAccounts.length }}</p>

    <AccountControlCard v-for="(account, index) in visibleManagedAccounts" v-bind:key="index" :account="account"
    @openClientDetails="openClientDetails"
    @goToAccount="goToAccount(account.clientId, account.nickname)"
    @cancelControl="cancelControl(account.clientId)" />

    <Modal v-if="showModal" :type="'clientDetails'" :actionType="'openClientDetails'" :clientId="openClientId" @closeModal="closeModal"/>
</div> 
</template>

<script>
import localizeFilter from '@/filters/localize.filter'
import AccountControlCard from '@/components/AccountControlCard.vue';
import Modal from "@/components/modals/Modal.vue"

export default {
  components: {
    AccountControlCard, Modal
  },
  data() { 
    return {
      requests: [],
      managedAccounts: [],
      openClientId: null,
      showModal: false,
      searchString: '',
      search: null
    }
  },
  watch: {
    searchString(newVal, oldVal) {
      if (newVal === '') {
        this.$nextTick(()=>{
          this.search = null
          this.$forceUpdate()
        })
      }
    }
  },
  computed: {
    visibleManagedAccounts() {
      console.log('force update')
      const list = JSON.parse(JSON.stringify(this.managedAccounts))
      if (this.search == null) {
        return list
      } else {
        let result = []
        for (let i=0; i<list.length; i++) {
            // console.log(list[i].nickname, this.search, list[i].nickname.startsWith(this.search))
            if ((list[i].nickname || '').startsWith(this.search)) {
              result.push(list[i])
              continue
            }
            const fullName = list[i].firstName  + ' ' + list[i].lastName
            // console.log(fullName, this.search, fullName.startsWith(this.search))
            if (fullName.startsWith(this.search)) {
              result.push(list[i])
              continue
            }
        }
        // console.log(result)
        return result
      }
    },
  },
  methods: {
    startSearch() {
      if (this.searchString !== '') {
        this.search = this.searchString.replace('@','')
      }
      this.$nextTick(()=>{
        this.$forceUpdate()
      })
    },
    closeModal() {
      this.showModal = false
    },
    openClientDetails(clientId) {
      this.openClientId = clientId;
      this.showModal = true;
    },

    async loadData(){
        // const {requests, managedAccounts} = {
        //     requests: [
        //         {
        //             nickname: 'dfdsjhjdshdjv',
        //             clientId: 100500
        //         },
        //         {
        //             nickname: 'iuuhu',
        //             clientId: 100500
        //         },
        //     ],
        //     managedAccounts: [
        //         {
        //             nickname: 'kkokok',
        //             clientId: 100500
        //         },
        //         {
        //             nickname: 'sawswsswsw',
        //             clientId: 100500
        //         },
        //         {
        //             nickname: 'xexexexexe',
        //             clientId: 100500
        //         },
        //     ]
        // }
        //   const _structure = {}
        //   const node = JSON.parse(JSON.stringify(_structure))
        //   _structure['nodes']=[JSON.parse(JSON.stringify(node)), JSON.parse(JSON.stringify(node))]
        //  _structure['nodes'][0]['nodes']= [JSON.parse(JSON.stringify(node))]
        //  _structure['nodes'][0]['nodes'][0]['nodes']= [JSON.parse(JSON.stringify(node))]
        const {requests, clients: managedAccounts} = await this.$store.dispatch('getManagedClientsAndRequestsByManager')
        this.requests = requests
        this.managedAccounts = managedAccounts
    },

    async getControl(requestId) {
        try {
            await this.$store.dispatch('confirmManagementRequest', {requestId})
            this.$message(localizeFilter('profile_updated'))
            await this.loadData()
        } catch(e) {
            this.$error(e)
        } 
    },

    async refuseControl(requestId) {
        try {
            await this.$store.dispatch('refuseManagementRequest', {requestId})
            this.$message(localizeFilter('profile_updated'))
            await this.loadData()
        } catch(e) {
            this.$error(e)
        } 
    },

    async cancelControl(managedClientId) {
        try {
            await this.$store.dispatch('cancelManagementByManager', {managedClientId})
            this.$message(localizeFilter('profile_updated'))
            await this.loadData()
        } catch(e) {
            this.$error(e)
        } 
    },

    async goToAccount(clientId, nickname) {
        this.$emit('startManaging', clientId, nickname)
    },


    
  },
  async mounted() {
    this.$emit('mountedChild', 'Structure');
    try {
        await this.loadData()
    }
    catch (e) {
      this.$error(e)
    }    
    
    
  }  
};
</script>

